import React, { useState } from "react";
import Header from "../Header";
import DialogLoading from "../DialogLoading";
import Dialog from "../../models/Dialog";
import { Link } from "react-router-dom";

function ProfileMessagesPage() {
  const dialogs = [new Dialog(), new Dialog()],
    [searchQuery, setSearchQuery] = useState("");
  return (
    <div className="app-page">
      <Header />
      <DialogLoading />
      <div className="container">
        <div className="dialog">
          <div className="dialog__box">
            <div className="">
              <div className="navigation__search">
                <input
                  className="t-input"
                  id="search"
                  type="text"
                  placeholder=""
                  name="search"
                  autocomplete="off"
                  onKeyUp={(event) => {
                    setSearchQuery(event.target.value);
                  }}
                />
              </div>
              <form className="dialog__users t-scroll">
                {dialogs.map((dialog) => (
                  <Link
                    to={`/profile/dialogs?id=${dialog.twilioSID}&peer=${dialog.peer.login}`}
                  >
                    <div
                      className="dialog__user"
                      style={{
                        display:
                          !searchQuery.length ||
                          `${dialog.peer.name} ${dialog.peer.surname}`.match(
                            new RegExp(searchQuery, "i")
                          )
                            ? "block"
                            : "none",
                      }}
                    >
                      <div
                        className="user__img"
                        style={{
                          backgroundImage: `url(/${dialog.peer.profilePic})`,
                        }}
                      ></div>
                      <div className="user__info">
                        <div className="user__name">
                          {dialog.peer.name} {dialog.peer.surname}
                        </div>
                        <div
                          className="user__text"
                          id={`lastMessageText-${dialog.twilioSID}`}
                        ></div>
                      </div>
                      <div
                        className="dialog__time"
                        id={`lastMessageTime-${dialog.twilioSID}`}
                      ></div>
                      <div
                        className="dialog__count"
                        id={`unreadCount-${dialog.twilioSID}`}
                      ></div>
                    </div>
                  </Link>
                ))}
              </form>
            </div>
            <div className="dialog__content"></div>
          </div>
        </div>
      </div>
      <script src="https://media.twiliocdn.com/sdk/js/chat/v3.3/twilio-chat.min.js"></script>
      <script src="/js/messages.js"></script>
    </div>
  );
}

export default ProfileMessagesPage;
