import React, { useState } from "react";
import CSRF from "./CSRF";
import useBus, { dispatch } from "use-bus";
import { className } from "../utils";
import { openModal } from "./Modal";

function AuthenticatedMenu(props) {
  return (
    <div
      id="menu"
      className={className({ active: props.open })}
      style={props.style}
    >
      <div id="menu__close" onClick={closeMenu}>
        <img className="img-svg" src="/images/icons/close.svg" alt="" />
      </div>
      <div className="menu__list">
        <a className="menu__link active" href="/all-jobs" onClick={closeMenu}>
           
        </a>
        <a className="menu__link" href="/profile/jobs" onClick={closeMenu}>
           
        </a>
        <a className="menu__link" href="/profile/pending" onClick={closeMenu}>
          
        </a>
        <a className="menu__link" href="/profile/messages" onClick={closeMenu}>
          
        </a>
      </div>
      <form method="POST" action="/logout">
        <CSRF />
        <button type="submit">  </button>
      </form>
    </div>
  );
}

function NotAuthenticatedMenu(props) {
  return (
    <div
      className={className({ menu_unregistered: true, active: props.open })}
      id="menu"
      style={props.style}
    >
      <div id="menu__close" onClick={closeMenu}>
        <img className="img-svg" src="/images/icons/close.svg" alt="" />
      </div>
      <a className="logo" href="#">
        tutoria
      </a>
      <div className="menu__list">
        <a className="menu__link" href="/all-jobs" onClick={closeMenu}>
           
        </a>
      </div>
      <div className="modal__open" onClick={() => openModal("signin")}>
        
      </div>
    </div>
  );
}

function Menu(props) {
  const [isAuthenticated] = useState(true),
    [open, setOpen] = useState(false),
    style = { display: open ? "block" : "none" },
    openMenu = () => {
      setOpen(true);
      if (props.onOpen) {
        props.onOpen();
      }
    },
    closeMenu = () => {
      setOpen(false);
      if (props.onClose) {
        props.onClose();
      }
    };

  useBus("MENU_MUST_OPEN", openMenu);
  useBus("MENU_MUST_CLOSE", closeMenu);

  return isAuthenticated ? (
    <AuthenticatedMenu open={open} style={style} />
  ) : (
    <NotAuthenticatedMenu open={open} style={style} />
  );
}

export default Menu;
export function openMenu() {
  dispatch("MENU_MUST_OPEN");
}
export function closeMenu() {
  dispatch("MENU_MUST_CLOSE");
}
