import React, { useState } from "react";
import Header from "../Header.js";
import User from "../../models/User.js";
import CSRF from "../CSRF.js";
import { forceNumericOnly, className } from "../../utils";
import InputMask from "react-input-mask";
import Modal, { openModal } from "../Modal.js";
import Select from "../Select";

function sendConfirmationLink(event) {
  /*$.ajax({
    url: `/profile/send-confirmation?_csrf=${csrfToken}`,
    type: 'POST',
    success: function() {
      window.location.reload(false);
    }
  });*/
  event.preventDefault();
  alert("Confirmaton link sent!");
  return false;
}

function ProfileMePage() {
  const user = new User(),
    [withdrawalMethodError, setWithdrawalMethodError] = useState(false),
    [withdrawalMethod, setWithdrawalMethod] = useState({
      value: null,
      label: "",
    });

  function checkWithdrawalMethod() {
    if (["qiwi", "card"].indexOf(withdrawalMethod.value) + 1) {
      return openModal(withdrawalMethod.value);
    } else if (!withdrawalMethod.value) {
      return;
    } else {
      return setWithdrawalMethodError(true);
    }
  }

  return (
    <div className="app-page">
      <Header />
      <div className="container">
        <div className="warningMessage active">
          <img className="message-icon" src="/images/bookmark.svg" alt="" />
          <div className="message-close" id="message-{lose">
            <img src="/images/icons/close.svg" alt="" />
          </div>
          <div className="message-content">
            <div className="message-title">  Email</div>
            <div className="message-description">
                       .{" "}
              <br />
              {" "}
              <a
                id="sendConfirmationLink"
                href="#"
                onClick={sendConfirmationLink}
              >
                
              </a>
              ,    .
            </div>
          </div>
        </div>
      </div>
      <div className="user">
        <div className="error__box" id="errorBox">
          <div className="error__text" id="errorText" />
        </div>
        <div className="container user__cont">
          <div className="user__left">
            <a className="modal__open" onClick={() => openModal("avatar")}>
              <div className="user__head">
                <div
                  className="head__img"
                  style={{ backgroundImage: `url(/${user.profilePic})` }}
                />
                <div className="head__info">
                  <div className="user__name">
                    {user.name} {user.surname}
                  </div>
                  <div className="user__nickname">{user.login}</div>
                </div>
              </div>
            </a>
            <div className="user__info">
              <div className="info__label">
                <div className="info__label"> . :</div>
                <div className="info__value">{user.email}</div>
                <input
                  className="info__input"
                  type="text"
                  name="email"
                  value={user.email}
                />
                <div className="info__edit">
                  <button
                    className="modal__open"
                    onClick={() => openModal("email")}
                    style={{ backgroundColor: "transparent", outline: "none" }}
                  >
                    <img
                      className="img-svg"
                      src="/images/icons/edit.svg"
                      alt=""
                    />
                  </button>
                </div>
              </div>
              <div className="info__label">
                <div className="info__label">:</div>
                <div className="info__value">{user.age} </div>
                <input
                  className="info__input"
                  type="text"
                  name="age"
                  value={user.age}
                />
                <div className="info__edit">
                  <img
                    className="img-svg"
                    src="/images/icons/edit.svg"
                    alt=""
                  />
                </div>
              </div>
              <div className="info__label">
                <div className="info__label"> tutoria:</div>
                <div className="info__value">{user.timeString}</div>
              </div>
              <div className="info__label">
                <div className="info__label">:</div>
                <div className="info__value">{user.balance} </div>
                <div className="info__edit-no-modify">
                  <button
                    className="modal__open"
                    onClick={() => openModal("deposit")}
                    style={{ backgroundColor: "transparent", outline: "none" }}
                  >
                    <img
                      className="img-svg"
                      src="/images/icons/card_1_add.svg"
                      alt=""
                    />
                  </button>
                  <button
                    className="modal__open"
                    onClick={() => openModal("withdraw")}
                    style={{ backgroundColor: "transparent", outline: "none" }}
                  >
                    <img
                      className="img-svg"
                      src="/images/icons/card_1_minus.svg"
                      alt=""
                    />
                  </button>
                </div>
              </div>
            </div>
            <button
              className="user__password modal__open t-btn default"
              onClick={() => openModal("password")}
            >
               
            </button>
            <a
              className="user__password t-btn default"
              href="/profile/check-bills"
            >
               
            </a>
          </div>
          {user.reviews.length === 0 ? (
            <div className="user__right">
              <div className="right__title">
                  <br /> 
              </div>
              <div className="right__subtitle">  </div>
              {!user.isWorker ? (
                <form
                  method="POST"
                  action="/profile/start-working"
                  id="workerForm"
                >
                  <input type="hidden" name="userID" value={user.id} />
                  <CSRF />
                  <button className="right__btn t-btn blue" type="submit">
                     
                  </button>
                </form>
              ) : (
                ""
              )}
            </div>
          ) : (
            <div className="user__right absolute">
              <div className="right__title">
                 
                <br /> 
              </div>
              <div className="right__subtitle">
                {user.rating} / 5  {user.jobCountString}{" "}
              </div>
              <div className="user__reviews">
                <div className="reviews__box">
                  {user.reviews.map((review) => (
                    <div className="review">
                      <div className="rating-area">
                        {[5, 4, 3, 2, 1].map((rating) => (
                          <label
                            for={`1star-${rating}`}
                            title={` ${rating}`}
                          >
                            <input
                              type="radio"
                              id={`1star-${rating}`}
                              name="1rating"
                              value={rating}
                              checked={review.rating === rating}
                            />
                          </label>
                        ))}
                      </div>
                      <div className="review__text">{review.body}</div>
                      <div className="review__author">
                        {review.author.name} {review.author.surname}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      <Modal name="password" title=" ">
        <form
          className="modal__form"
          action="/profile/change-password"
          method="POST"
          id="changePassForm"
        >
          <div className="modal__line">
            <div className="modal__label"> </div>
            <input
              className="modal__input"
              type="password"
              name="curPassword"
              required="required"
              placeholder=" "
            />
          </div>
          <div className="modal__line">
            <div className="modal__label"> </div>
            <input
              className="modal__input"
              type="password"
              name="newPassword"
              required="required"
              placeholder=" "
            />
          </div>
          <div className="modal__line">
            <div className="modal__label">   </div>
            <input
              className="modal__input"
              type="password"
              name="confirmPassword"
              required="required"
              placeholder="   "
            />
          </div>
          <CSRF />
          <button className="t-btn blue" type="submit">
             
          </button>
        </form>
      </Modal>
      <Modal title="  . " name="email">
        <form
          className="modal__form"
          action="/profile/change-email"
          method="POST"
          id="emailForm"
        >
          <div className="modal__line">
            <div className="modal__label"> email</div>
            <input
              className="modal__input"
              type="email"
              name="curEmail"
              required="required"
              placeholder=" email"
            />
          </div>
          <div className="modal__line">
            <div className="modal__label"> email</div>
            <input
              className="modal__input"
              type="email"
              name="newEmail"
              required="required"
              placeholder=" email"
            />
          </div>
          <div className="modal__line">
            <div className="modal__label"> email</div>
            <input
              className="modal__input"
              type="email"
              name="confirmEmail"
              required="required"
              placeholder=" email"
            />
          </div>
          <CSRF />
          <button className="t-btn blue" type="submit">
              . 
          </button>
        </form>
      </Modal>
      <Modal
        title="  !"
        name="withdraw"
        className={className({
          model__withdraw: true,
          error: withdrawalMethodError,
        })}
        onClose={() => {
          setWithdrawalMethodError(false);
          setWithdrawalMethod({ value: null, label: "" });
        }}
      >
        <div className="error__text">  !</div>
        <div className="modal__form">
          <div className="modal__line">
            <div className="modal__label"> </div>
            <div className="control__input">
              <Select
                options={[
                  { value: "card", label: "  " },
                  { value: "qiwi", label: "  Qiwi " },
                ]}
                onChange={(option) => setWithdrawalMethod(option)}
                name="method"
                placeholder=" "
              />
            </div>
            <img
              className="img-svg error__icon"
              src="/images/icons/alert.svg"
            />
          </div>
          <button
            className="t-btn blue"
            type="submit"
            onClick={checkWithdrawalMethod}
          >
            
          </button>
        </div>
      </Modal>
      <Modal
        name="qiwi"
        title="   Qiwi "
        className="modal__qiwi"
      >
        <div className="error__text">  100</div>
        <form
          className="modal__form"
          id="qiwiWithdrawForm"
          method="POST"
          action="/profile/withdraw-qiwi"
        >
          <div className="modal__line">
            <div className="modal__label"> Qiwi </div>
            <InputMask
              className="modal__input"
              name="qiwiNum"
              id="qiwi__mask"
              type="tel"
              autoComplete={false}
              required={true}
              placeholder=""
              mask="+99999999999"
            />
          </div>
          <div className="modal__line">
            <div className="modal__label">  </div>
            <div className="control__input">
              <input
                className="modal__input"
                type="tel"
                name="amount"
                autocomplete="off"
                required="required"
                placeholder=""
                id="qiwiAmountInput"
                onKeyDown={forceNumericOnly}
                onKeyUp={(e) => {
                  const amount = +e.target.value;
                  document.querySelector("#qiwiAmountSpan").innerHTML =
                    amount +
                    Math.round((amount * 0.07 + Number.EPSILON) * 100) / 100;
                }}
              />
            </div>
          </div>
          <CSRF />
          <div className="modal__warning">    : 7%</div>
          <div className="modal__warning">
              : <span id="qiwiAmountSpan">0</span> 
          </div>
          <button className="t-btn blue" type="submit">
            
          </button>
        </form>
      </Modal>
      <Modal className="modal__card" name="card" title="  ">
        <div className="error__text">  100</div>
        <form
          className="modal__form"
          id="cardWithdrawForm"
          method="POST"
          action="/profile/withdraw-card"
        >
          <div className="modal__line">
            <div className="modal__label"> </div>
            <div className="control__input">
              <Select
                id="card__type"
                name="cartType"
                required={true}
                options={[
                  { value: "Visa", label: "Visa" },
                  { value: "Mastercard", label: "Mastercard" },
                  { value: "Mir", label: "" },
                  { value: "Qiwi", label: "  Qiwi" },
                ]}
                placeholder=" "
              />
            </div>
          </div>
          <div className="modal__line">
            <div className="modal__label"> </div>
            <InputMask
              className="modal__input"
              id="card__mask"
              type="tel"
              name="cardNum"
              autoComplete={false}
              required={true}
              placeholder=""
              mask="9999 9999 9999 9999"
            />
          </div>
          <div className="modal__line">
            <div className="modal__label">  </div>
            <div className="control__input">
              <input
                className="modal__input"
                id="cardAmountInput"
                type="tel"
                name="amount"
                autocomplete="off"
                required="required"
                placeholder=""
                onKeyDown={forceNumericOnly}
                onKeyUp={(e) => {
                  const amount = +e.target.value;
                  document.querySelector("#cardAmountSpan").innerHTML =
                    amount +
                    Math.round((amount * 0.05 + Number.EPSILON) * 100) / 100 +
                    50;
                }}
              />
              
            </div>
          </div>
          <div className="modal__warning">
                : 5% + 50 
          </div>
          <div className="modal__warning">
              : <span id="cardAmountSpan">0</span> 
          </div>
          <CSRF />
          <button className="t-btn blue" type="submit">
            
          </button>
        </form>
      </Modal>
      <Modal name="deposit" title=" ">
        <form
          className="modal__form"
          action="/profile/deposit-funds"
          method="POST"
          id="depositForm"
        >
          <div className="modal__line">
            <div className="modal__label"> </div>
            <input
              className="modal__input"
              type="text"
              name="amount"
              required="required"
              placeholder=" "
            />
          </div>
          <CSRF />
          <div className="modal__warning">
                Qiwi : 0%
          </div>
          <div className="modal__warning">
                : 1%
          </div>
          <button className="t-btn blue" type="submit">
               
          </button>
        </form>
      </Modal>
      <Modal name="avatar" title="  ">
        <form
          className="modal__form"
          action="/profile/edit-avatar"
          method="POST"
          id="avatarForm"
          enctype="multipart/form-data"
        >
          <div className="modal__line">
            <div className="modal__label">  </div>
            <input
              className="modal__input"
              type="file"
              name="avatar"
              required="required"
            />
          </div>
          <button className="t-btn blue" type="submit">
            
          </button>
        </form>
      </Modal>
    </div>
  );
}

export default ProfileMePage;
