import React, { useState } from "react";
import { Link } from "react-router-dom";
import Job from "../../models/Job";
import Header from "../Header";
import Pagination from "../Pagination";
import Footer from "../Footer";

function AllJobsPage() {
  const [jobs] = useState([new Job()]);

  return (
    <div className="app-page">
      <Header />
      <div className="catalog">
        <div className="container catalog__cont">
          <div className="catalog__content">
            <div className="t-title"> </div>
            <div className="content__box">
              {jobs.map((job) => (
                <Link
                  key={job.id}
                  className="item"
                  to={`/job-detail/${job.id}`}
                >
                  <div className="item__head">
                    <div className="thing single">
                      <span>{job.subject}</span>
                    </div>
                    <div className="item__title">{job.title}</div>
                    <div className="item__date">{job.timeString}</div>
                  </div>
                  <div className="item__foot">
                    <div
                      className="item__author"
                      style={{
                        backgroundImage: `url(/${job.owner.profilePic})`,
                      }}
                    ></div>
                    <div className="item__price">{job.price}&#x20bd;</div>
                  </div>
                </Link>
              ))}
            </div>
            <Pagination />
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default AllJobsPage;
