import React from "react";
import { Link, useLocation } from "react-router-dom";

function search(object) {
  return "?" + new URLSearchParams(object).toString();
}

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function Pagination() {
  const query = useQuery(),
    currentPage = parseInt(query.get("page") || 1),
    lastPage = 33,
    hasPreviousPage = currentPage > 1,
    hasNextPage = currentPage < lastPage,
    hasPrePreviousPage = currentPage > 2,
    hasPostNextPage = currentPage < lastPage - 1;
  return (
    <div className="t-pagination">
      {hasPreviousPage ? (
        <Link
          to={{ search: search({ page: currentPage - 1 }) }}
          className="prev"
        >
          <img className="img-svg" src="/images/icons/pagination.svg" alt="" />
        </Link>
      ) : (
        <a className="prev" onClick={() => false}>
          <img className="img-svg" src="/images/icons/pagination.svg" alt="" />
        </a>
      )}
      {hasPreviousPage ? (
        <Link className="app-page" to={{ search: search({ page: 1 }) }}>
          1
        </Link>
      ) : (
        ""
      )}
      {hasPrePreviousPage ? (
        <Link
          className="app-page"
          to={{ search: search({ page: currentPage - 1 }) }}
        >
          {currentPage - 1}
        </Link>
      ) : (
        ""
      )}
      <Link
        className="app-page active"
        to={{ search: search({ page: currentPage }) }}
      >
        {currentPage}
      </Link>
      {hasPostNextPage ? (
        <Link
          className="app-page"
          to={{ search: search({ page: currentPage + 1 }) }}
        >
          {currentPage + 1}
        </Link>
      ) : (
        ""
      )}
      {hasNextPage ? (
        <Link className="app-page" to={{ search: search({ page: lastPage }) }}>
          {lastPage}
        </Link>
      ) : (
        ""
      )}
      {hasNextPage ? (
        <Link
          className="next"
          to={{ search: search({ page: currentPage + 1 }) }}
        >
          <img className="img-svg" src="/images/icons/pagination.svg" alt="" />
        </Link>
      ) : (
        <a className="next" onClick={() => false}>
          <img className="img-svg" src="/images/icons/pagination.svg" alt="" />
        </a>
      )}
    </div>
  );
}

export default Pagination;
