import React, { useState } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import LandingPage from "./components/pages/LandingPage";
import AllJobsPage from "./components/pages/AllJobsPage";
import JobDetailPage from "./components/pages/JobDetailPage";
import UserDetailPage from "./components/pages/UserDetailPage";
import CSRF from "./components/CSRF";
import SignUpPage from "./components/pages/SignUpPage";
import ResetPasswordPage from "./components/pages/ResetPasswordPage";
import Error404Page from "./components/pages/Error404Page";
import Error400Page from "./components/pages/Error400Page";
import Error403Page from "./components/pages/Error403Page";
import Error500Page from "./components/pages/Error500Page";
import ProfileMePage from "./components/pages/ProfileMePage";
import ProfileEditJobPage from "./components/pages/ProfileEditJobPage";
import ProfileAddJobPage from "./components/pages/ProfileAddJobPage";
import ProfileDialogPage from "./components/pages/ProfileDialogPage";
import ProfileJobProposalsPage from "./components/pages/ProfileJobProposalsPage";
import ProfileJobsPage from "./components/pages/ProfileJobsPage";
import ProfileMessagesPage from "./components/pages/ProfileMessagesPage";
import ProfilePendingJobsPage from "./components/pages/ProfilePendingJobsPage";
import ProfileToDoJobsPage from "./components/pages/ProfileToDoJobsPage";
import ProfileTryJobPage from "./components/pages/ProfileTryJobPage";
import Modal, { openModal } from "./components/Modal";
import Menu from "./components/Menu";

function App() {
  const [isAuthenicated] = useState(false),
    [menuOpen, setMenuOpen] = useState(false);

  return (
    <div className="app" style={{ overflow: menuOpen ? "hidden" : "auto" }}>
      <Router>
        <Switch>
          <Route path="/" exact component={LandingPage} />
          <Route path="/all-jobs" component={AllJobsPage} />
          <Route path="/job-detail/:id" exact component={JobDetailPage} />
          <Route path="/user-detail/:login" exact component={UserDetailPage} />
          <Route path="/signup" exact component={SignUpPage} />
          <Route path="/reset/:token" exact component={ResetPasswordPage} />
          <Route path="/profile/me" exact component={ProfileMePage} />
          <Route path="/profile/add-job" exact component={ProfileAddJobPage} />
          <Route path="/profile/dialogs" exact component={ProfileDialogPage} />
          <Route
            path="/profile/messages"
            exact
            component={ProfileMessagesPage}
          />
          <Route
            path="/profile/jobs/:id/edit"
            exact
            component={ProfileEditJobPage}
          />
          <Route
            path="/profile/jobs/proposals"
            exact
            component={ProfileJobProposalsPage}
          />
          <Route path="/profile/jobs/" exact component={ProfileJobsPage} />
          <Route
            path="/profile/pending"
            exact
            component={ProfilePendingJobsPage}
          />
          <Route path="/profile/todo" exact component={ProfileToDoJobsPage} />
          <Route path="/profile/jobs/try" exact component={ProfileTryJobPage} />
          {/* Just to test */}
          <Route path="/400" exact component={Error400Page} />
          <Route path="/403" exact component={Error403Page} />
          <Route path="/500" exact component={Error500Page} />
          {/* Tests finished */}
          <Route component={Error404Page} />
        </Switch>
      </Router>

      <Modal name="signin" className="signin__modal" title="">
        <form
          className="modal__form"
          method="POST"
          action="/login"
          id="loginForm"
        >
          <CSRF />
          <div className="modal__line">
            <div className="modal__label">  . </div>
            <input
              className="modal__input"
              type="text"
              name="login"
              required="required"
              placeholder=""
            />
          </div>
          <div className="modal__line">
            <div className="modal__label"></div>
            <input
              className="modal__input"
              type="password"
              name="password"
              required="required"
              placeholder=""
            />
          </div>
          <button className="t-btn blue" type="submit">
            
          </button>
          <div className="modal__social">
            <div className="social__btns">
              <a className="social__btn" href="/vk-auth">
                <img src="/images/icons/social/vk.svg" />
              </a>
              <a className="social__btn" href="/google-auth">
                <img src="/images/icons/social/google.svg" />
              </a>
            </div>
          </div>
        </form>
        <div className="modal__additional">
          <a
            className="additional__btn modal__open"
            onClick={() => openModal("reset")}
          >
             
          </a>
          <a className="additional__btn" href="/signup">
            
          </a>
        </div>
      </Modal>
      <Modal
        name="reset"
        className="modal__signin"
        title=" "
      >
        <form
          className="modal__form"
          method="POST"
          action="/reset"
          id="resetForm"
        >
          <CSRF />
          <div className="modal__line">
            <div className="modal__label"> . </div>
            <input
              className="modal__input"
              type="email"
              name="email"
              required="required"
              placeholder=" . "
            />
          </div>
          <div className="modal__warning">
                    
          </div>
          <button className="t-btn blue" type="submit">
            
          </button>
        </form>
        <div className="modal__additional">
          <a
            className="additional__btn modal__open"
            onClick={() => openModal("signin")}
          >
            
          </a>
          <a className="additional__btn" href="/signup">
            
          </a>
        </div>
      </Modal>

      <Menu
        onOpen={() => setMenuOpen(true)}
        onClose={() => setMenuOpen(false)}
      />
    </div>
  );
}

export default App;
