import React, { useState, createRef } from "react";

function UploadBox(props) {
  const [files, setFiles] = useState([]);
  return (
    <div className="upload__box">
      <div className="upload__items">
        {files.map((file) => (
          <div className="upload__item">
            <div className="upload__name">{file.name}</div>
          </div>
        ))}
      </div>
      <div
        className="upload__btn t-btn default"
        onClick={(event) => {
          let input = event.target.querySelector("input.upload__event");
          if (input) {
            input.dispatchEvent(new MouseEvent("click"));
          }
        }}
      >
        <input
          className="upload__input"
          type="file"
          name={props.name}
          multiple={props.multiple ? "multiple" : null}
          id={props.id}
          onChange={(event) => {
            console.log(event);
            let newFiles = Array.from(files);
            newFiles.push(...Array.from(event.target.files));
            setFiles(newFiles);
          }}
        />
        <div className="upload__label">
          {props.multiple && files.length && props.moreLabel
            ? props.moreLabel
            : props.label}
        </div>
      </div>
      <div className="upload__result" />
    </div>
  );
}

export default UploadBox;
