import React, { useState } from "react";
import useBus, { dispatch } from "use-bus";
import useEventListener from "@use-it/event-listener";
import PropTypes from "prop-types";
import Pagination from "./Pagination";
import { closeMenu } from "./Menu";

function Modal(props) {
  const [open, setOpen] = useState(false),
    style = {
      display: open ? "block" : "none",
    };

  function openModal() {
    setOpen(true);
    if (props.onOpen) {
      props.onOpen();
    }
  }

  function closeModal() {
    setOpen(false);
    if (props.onClose) {
      props.onClose();
    }
  }

  // Subscribe to modal open events
  useBus("MODAL_MUST_OPEN", (event) => {
    if (event.payload.modalName === props.name) {
      openModal();
    }
  });

  // Subscribe to modal close events
  useBus("MODALS_MUST_CLOSE", () => closeModal());

  // Subscribe to keyboard-driven close events
  useEventListener("keydown", (event) => {
    if (event.keyCode === 27) {
      event.stopPropagation();
      closeModal();
    }
  });

  return (
    <div className="modal__outer">
      <div className="overlay" onClick={() => closeModal()} style={style}></div>
      <div
        className={`modal ${props.className}`}
        id={`${props.name}Modal`}
        style={style}
      >
        <div className="modal__close" onClick={() => closeModal()}>
          <img className="img-svg" src="/images/icons/close.svg" alt="" />
        </div>
        <div className="modal__title">{props.title}</div>
        {props.children}
      </div>
    </div>
  );
}

Modal.propTypes = {
  name: PropTypes.string,
  onOpen: PropTypes.func,
  onClose: PropTypes.func,
  title: PropTypes.string,
};

export default Modal;
export function openModal(modalName) {
  dispatch({ type: "MODALS_MUST_CLOSE" });
  closeMenu();
  dispatch({ type: "MODAL_MUST_OPEN", payload: { modalName } });
}
