import React, { useState } from "react";
import Job from "../../models/Job";
import Header from "../Header";
import Filter from "../Filter";
import { Link } from "react-router-dom";
import Pagination from "../Pagination";
import CSRF from "../CSRF";
import { className } from "../../utils";

function ProfileJobsPage() {
  const [jobs] = useState([new Job(), new Job()]);
  return (
    <div className="app-page">
      <Header />
      <div className="catalog">
        <div className="container catalog__cont">
          <Filter />
          <div className="catalog__content posts__content">
            <div className="t-title lg"> </div>
            <div className="content__box">
              <Link
                className="item add__item"
                to="/profile/add-job"
                style={{ backgroundImage: "url(/images/dashed.png)" }}
              >
                <div className="add__icon">
                  <img className="img-svg" src="/images/icons/add.svg" alt="" />
                </div>
                <div classs="item__title">
                  
                  <br />
                  
                </div>
              </Link>
              {jobs.map((job) => (
                <div className="item">
                  <div className="item__head">
                    <a className="thing single" href="">
                      <span> </span>
                    </a>
                    <a className="item__title" href="#">
                      {job.title}
                    </a>
                    <div className="item__date">{job.timeString}</div>
                  </div>
                  {job.isPending ? (
                    <div className="item__foot">
                      <div className="item__foot-top">
                        <div className="foot__price">
                          <div className="title"></div>
                          <div className="price">{job.price} </div>
                        </div>
                        <a
                          className="item__author"
                          href="#"
                          style={{
                            backgroundImage: `url(/${job.executant.user.profilePic})`,
                          }}
                        ></a>
                      </div>
                      <div className="item__foot-bottom">
                        <a
                          className={className({
                            "t-btn": true,
                            transparent__blue: true,
                            disabled: job.proposals.length === 0,
                          })}
                          href={`/profile/dialogs?id=${job.conversation.twilioSID}&peer=${job.conversation.peer.login}`}
                        >
                          
                        </a>
                      </div>
                    </div>
                  ) : (
                    <div className="item__foot">
                      <div className="item__foot-top">
                        <div className="foot__price">
                          <div className="title"></div>
                          <div className="price">{job.price} </div>
                        </div>
                        <div className="foot__buttons">
                          <a
                            className="t-action"
                            href={`/profile/edit-job/${job.id}`}
                          >
                            <img
                              className="img-svg"
                              src="/images/icons/action/edit.svg"
                              alt=""
                            />
                          </a>
                          <form action="/profile/delete-job" method="POST">
                            <input type="hidden" value={job.id} name="jobId" />
                            <CSRF />
                            <button className="t-action" type="submit">
                              <img
                                className="img-svg"
                                src="/images/icons/action/delete.svg"
                                alt=""
                              />
                            </button>
                          </form>
                        </div>
                      </div>
                      <div className="item__foot-bottom">
                        <a
                          className="t-btn transparent__blue"
                          onClick={() => job.proposals.length}
                          href={`/profile/show-proposals/${job.id}`}
                        >
                          
                        </a>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
            <Pagination />
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProfileJobsPage;
