import React, { useState } from "react";
import CSRF from "./CSRF";
import User from "../models/User";
import { className } from "../utils";
import { useLocation } from "react-router-dom";
import { openMenu } from "./Menu";
import { openModal } from "./Modal";

function AuthenticatedHeader() {
  const [currentUser] = useState(new User()),
    path = useLocation().pathname;
  return (
    <header className="header" id="header">
      <div className="container header__cont">
        <div className="header__logo">
          <a className="logo" href="/">
            tutoria
          </a>
        </div>
        <div className="header__navigation">
          <div className="navigation__add">
            <a href="/profile/add-job">
              <img src="/images/icons/add.svg" />
            </a>
          </div>
          <div className="navigation__menu">
            <a
              className={className({
                menu__link: true,
                active: path === "/all-jobs",
              })}
              href="/all-jobs"
            >
               
            </a>
            <a
              className={className({
                menu__link: true,
                active: path === "/profile/jobs",
              })}
              href="/profile/jobs"
            >
               
            </a>
            <a
              className={className({
                menu__link: true,
                active: path === "/profile/pending",
              })}
              href="/profile/pending"
            >
              
            </a>
            {currentUser.isWorker ? (
              <a
                className={className({
                  menu__link: true,
                  active: path === "/profile/todo",
                })}
                href="/profile/todo"
              >
                 
              </a>
            ) : (
              ""
            )}
          </div>
        </div>
        <a className="header__message" href="/profile/messages">
          <img className="img-svg" src="/images/messaging.svg" alt="" />
        </a>
        <div className="header__user">
          <a
            className="user__link"
            href="/profile/me"
            style={{ backgroundImage: `url(/${currentUser.profilePic})` }}
          ></a>
          <form className="user__logout-form" method="POST" action="/logout">
            <CSRF />
            <button className="user__logout" type="submit">
              <img className="img-svg" src="/images/icons/close.svg" alt="" />
            </button>
          </form>
        </div>
        <div className="header__mobile" id="menu__open" onClick={openMenu}>
          <img className="img-svg" src="/images/icons/menu.svg" alt="" />
        </div>
      </div>
    </header>
  );
}

function NotAuthenticatedHeader() {
  return (
    <header className="header unregistered" id="header">
      <div className="container header__cont">
        <div className="header__logo">
          <a className="logo" href="/">
            tutoria
          </a>
        </div>

        <a
          className="t-btn default header__mobile-user modal__open"
          onClick={() => openModal("signin")}
        >
          <img src="/images/icons/header__user.png" alt="" />
        </a>
        <div className="header__navigation">
          <div className="navigation__menu">
            <a className="menu__link active" href="/all-jobs">
               
            </a>
          </div>
        </div>
        <button
          onClick={() => openModal("signin")}
          className="t-btn default header__signin modal__open"
        >
          
        </button>
        <a className="t-btn transparent__blue header__signup" href="/signup">
          
        </a>
        <div className="header__mobile" id="menu__open" onClick={openMenu}>
          <img className="img-svg" src="/images/icons/menu.png" alt="" />
        </div>
      </div>
    </header>
  );
}

function Header() {
  const [isAuthenticated] = useState(true);
  return isAuthenticated ? <AuthenticatedHeader /> : <NotAuthenticatedHeader />;
}

export default Header;
