import React, { useState } from "react";
import User from "../../models/User";
import CSRF from "../CSRF";
import { useParams } from "react-router-dom";
import Header from "../Header";

function UserDetailPage() {
  const urlParams = useParams(),
    [user] = useState(new User(urlParams.login)),
    [isAuthenticated] = useState(false);
  return (
    <div className="app-app-page">
      <Header />
      <div className="user">
        <div className="container user__cont">
          <div className="user__left">
            <div className="user__head">
              <div
                className="head__img"
                style={{
                  backgroundImage: `url(/${user.profilePic})`,
                }}
              ></div>
              <div className="head__info">
                <div className="user__name">
                  {user.name} {user.surname}
                </div>
                <div className="user__nickname">{user.login}</div>
              </div>
            </div>
            <div className="user__info">
              <div className="info__label">
                <div className="info__label">:</div>
                <div className="info__value">{user.email}</div>
              </div>
              <div className="info__label">
                <div className="info__label">:</div>
                <div className="info__value">{user.age} </div>
              </div>
              <div className="info__label">
                <div className="info__label"> tutoria:</div>
                <div className="info__value">{user.timeString}</div>
              </div>
            </div>
            {isAuthenticated ? (
              <form method="POST" action="/profile/send-message">
                <input type="hidden" name="peer" value={user.login} />
                <CSRF />
                <button type="submit" className="t-btn blue">
                   
                </button>
              </form>
            ) : (
              ""
            )}
          </div>
          {user.reviews.length ? (
            <div className="user__right absolute">
              <div className="right__title">
                 
                <br /> 
              </div>
              <div className="right__subtitle">
                {user.rating} / 5  {user.jobCountString}
              </div>
              <div className="user__reviews">
                <div className="reviews__box">
                  {user.reviews.map((review) => (
                    <div className="review">
                      <div className="rating-area">
                        {[5, 4, 3, 2, 1].map((rating) => (
                          <label
                            for={`1star-${rating}`}
                            title={` ${rating}`}
                          >
                            <input
                              type="radio"
                              id={`1star-${rating}`}
                              name="1rating"
                              value={rating}
                              checked={review.rating===rating}
                            />
                          </label>
                        ))}
                      </div>
                      <div className="review__text">{review.body}</div>
                      <div className="review__author">
                        {review.author.name} {review.author.surname}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
              {/* Pagination */}
            </div>
          ) : (
            <div className="user__right">
              {user.isWorker ? (
                <div className="right__title not-pass">
                  {user.name}    
                </div>
              ) : (
                <div>
                  <div className="right__title not-pass">
                    {user.name}    
                    <br />
                     
                  </div>
                  <div className="user__reviews">
                    <img
                      className="blur__img"
                      src="/images/blue_reviews.png"
                      alt=""
                    />
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default UserDetailPage;
